<template>
  <a-row id="main">
    <a-col :span="24" class="header-wrapper">
      <a-col :span="6" :offset="2">标题</a-col>
    </a-col>
    <a-col :span="20" :offset="2" class="content-wrapper">
      <p>1.上传项目文件</p>
      <div class="upload-area">
        <a-upload-dragger class="upload-button"
                          action="/upload"
                          accept=".zip">
          <p class="ant-upload-drag-icon">
            <a-icon type="inbox"/>
          </p>
          <p class="ant-upload-text">点击或拖动文件到此</p>
          <p class="ant-upload-hint">上传的文件内仅包含一个版本的项目</p>
        </a-upload-dragger>
        <div class="button-area">
          <a-button type="primary"
                    :loading="loading"
                    class="start-button"
                    size="large"
                    :disabled="!uploaded"
                    @click="launch"
                    block>
            开始计算
          </a-button>
        </div>
      </div>


      <div v-if="versionInfos.length>0">
        <p>2.计算结果</p>
        <div class="result-area">
          <a-tabs>
            <a-tab-pane v-for="(each,index) in versionInfos"
                        :key="index"
                        :tab="each.version">
              <versionInfo :version-info="each"/>
            </a-tab-pane>
          </a-tabs>
        </div>
        <p>3.预测结果</p>
        <div class="result-area">
          <a-tabs>
            <a-tab-pane tab="预测变更" key="0" class="predict-wrapper">
              <a-table :dataSource="predictDataSource"
                       :columns="predictTBColumns"
                       :pagination="{pageSize:10}" class="predict-data-wrapper">
                <template slot="className" slot-scope="name,{isChanged}">
                  <b class="font" :class="isChanged?'changed':'not-changed'">{{name}}</b>
                </template>
                <template slot="isChanged" slot-scope="data">
                  <div class="tag" :class="data?'changed':'not-changed'">
                    {{data?'变化':'不变'}}
                  </div>
                </template>
              </a-table>
              <div class="canvas-wrapper">
                <div id="chart">
                </div>
              </div>
            </a-tab-pane>
            <a-tab-pane tab="模型信息" key="1" class="list">
              <a-list :grid="{gutter:40,column:3}"
                      :dataSource="predict.model"
                      style="width:85%">
                <a-list-item slot="renderItem" slot-scope="item,index">
                  <div class="item-wrapper">
                    <div>{{item.name}}</div>
                    <div>{{item.value}}</div>
                  </div>
                </a-list-item>
              </a-list>
            </a-tab-pane>
          </a-tabs>
        </div>

      </div>
      <div v-else>
        <a-alert message="没有项目显示"
                 type="info"
                 showIcon
                 description="请从上方上传项目文件夹"/>
      </div>
    </a-col>
    <a-col :span="24" class="footer-wrapper">
      <div>制作者：张可<br/>制作日期:{{new Date("2019-6-5").toLocaleDateString()}}</div>
    </a-col>
  </a-row>
</template>
<script>
  import versionInfo from "./components/VersionInfo"
  import echarts from "echarts"
  import axios from "axios"

  export default {
    updated() {
      this.onComplete();
    },
    computed: {
      option() {
        let changedCount = this.predict.data.reduce(((pValue, cValue) => {
          !cValue.isChanged || pValue++;
          return pValue;
        }), 0);
        let label = ['将变化的', '将不变的'];
        return {
          title: {
            text: '预测变化占比',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{b}类数量: {c}'
          },
          legend: {
            type: "scroll",
            orient: 'vertical',
            right: 10,
            data: label
          },
          series: [{
            name: '变化占比',
            type: 'pie',
            data: [
              {name: label[0], value: changedCount},
              {name: label[1], value: this.predict.data.length - changedCount}
            ]
          }]
        }
      },
      predictDataSource() {
        if (this.predict.data.length === 0) return {};
        else return this.predict.data.map((e, i) => {
          return {
            key: i,
            ...e
          }
        })
      }
    },
    data() {
      let x=1;
      return {
        loading: false,
        uploaded: true,
        chart: null,
        fileList: [],
        predictTBColumns: [{
          title: "类名",
          dataIndex: "className",
          scopedSlots:{customRender:"className"}
        }, {
          title: "预测结果",
          dataIndex: "isChanged",
          scopedSlots: {customRender: "isChanged"}
        }],

        versionInfos: [
          {
            projectName: "junit 4",
            version: "4.1.0",
            basic: [{
              name: "key1",
              value: 123
            }, {
              name: "key2",
              value: 321
            }],
            metric: {
              head: ['className', 'type', 'changeValue', 'changeType'],
              data: [
                ['junit4.1', 'class', '16', 'updated'],
                ['junit4,3', 'interface', '10', 'added'],
                ['junit4,3', 'interface', '10', 'null']
              ]
            }
          }, {
            projectName: "junit 4",
            version: "4.2.0",
            basic: [{
              name: "key1",
              value: 123
            }, {
              name: "key2",
              value: 321
            }],
            metric: {
              head: ['className', 'type', 'changeValue', 'changeType'],
              data: [
                ['junit4.9', 'class', '16', 'updated'],
                ['junit4,9', 'interface', '10', 'added'],
                ['junit4,9', 'enum', '10', 'added'],
              ]
            }
          }],
        predict: {
          data: [{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: false
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          },{
            className: "org.junit.test2tttttttttttttttttttttttttttttttttttttt",
            isChanged: true
          }, {
            className: "org.junit.test3",
            isChanged: false
          }],
          model: {}
        }
      }
    },
    methods: {
      onComplete() {
        if (!this.chart) {
          this.chart = echarts.init(document.getElementById("chart"));
        }
        this.chart.setOption(this.option);
      },
      async launch() {
        this.loading = true;
        let data = await axios.get("/launch");
        console.log(data);
        if (data.status === 200) {
          this.versionInfos = data.data.data.versionInfo;
          this.predict = data.data.data.predict;
          this.onComplete();
        }
        this.loading = false;
      },
      test() {
        this.versionInfos = [
          {
            projectName: "junit 4",
            version: "4.1.0",
            basic: [{
              name: "key1",
              value: 123
            }, {
              name: "key2",
              value: 321
            }],
            metric: {
              head: ['className', 'type', 'changeValue', 'changeType', 'loc'],
              data: [
                ['junit4.9', 'class', '16', 'updated', 123],
                ['junit4,9', 'interface', '10', 'added', 222],
                ['junit4,9', 'enum', '10', 'added', 333],
              ]
            }
          }, {
            projectName: "junit 4",
            version: "4.7.0",
            basic: [{
              name: "key1",
              value: 555
            }],
            metric: {
              head: ['className', 'type', 'changeValue', 'changeType'],
              data: [
                ['junit4.1', 'class', '16', 'updated'],
                ['junit4,3', 'class', '10', 'added'],
                ['junit4,3', 'class', '10', 'added'],
                ['junit4,3', 'class', '10', 'added']
              ]
            }
          }]
      }
    },
    components: {
      versionInfo
    }
  }
</script>
