<template>
  <div>
    <a-tabs tab-position="left" @change="changeTab">
      <a-tab-pane tab="基本信息" key="1" class="list">
        <a-list :grid="{gutter:40,column:2}"
                :dataSource="versionInfo.basic" style="width:85%">
          <a-list-item slot="renderItem" slot-scope="item,index">
            <div class="item-wrapper">
              <div>{{item.name}}</div>
              <div>{{item.value}}</div>
            </div>
          </a-list-item>
        </a-list>
      </a-tab-pane>
      <a-tab-pane tab="度量信息" key="2" class="table">
        <a-table :dataSource="dataSource"
                 :columns="columns"
                 :pagination="{pageSize:10}"
                 :scroll="{x:true}"
        >
          <template slot="className" slot-scope="text">
            <div style="color:rgb(16, 142, 233)"><b>{{text}}</b></div>
          </template>
          <template slot="type" slot-scope="text">
            <div :class="'type-'+text">{{text}}</div>
          </template>
          <template slot="changeType" slot-scope="[type,sty,text]">
            <a-icon :type="type" :style="{color:sty}"/>
            {{text}}
          </template>
        </a-table>
        <div class="chart-wrapper">
          <div :id="versionInfo.version" style="width:800px;height:400px;"></div>
        </div>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
  import AListItem from "ant-design-vue/es/list/Item";
  import echarts from "echarts"

  export default {
    name: "VersionInfo",
    components: {AListItem},
    props: ['versionInfo'],
    data() {
      return {
        chart: null,
        cTypeSlot: {
          updated: ['sync', 'rgb(16, 142, 233)', '变动'],
          unchanged: ['check', '#bfbfbf', '不变'],
          deleted: ['minus', 'rgb(237, 50, 66)', '删除'],
          added: ['plus', '#0fbb5c', '新增'],
          null:['close','#bfbfbf','无对比信息']
        },
      }
    },
    computed: {
      title() {
        return ["类名", "类型", "变更值", "变化类型", ...this.versionInfo.metric.head.slice(4)]
      },
      columns() {
        return this.versionInfo.metric.head.map((each, index) => {
          let additive = index > 0 ? {width: 50} : {fixed: 'left'};
          return {
            title: this.title[index],
            dataIndex: each,
            scopedSlots: {customRender: each},
            ...additive,
            sorter: (a, b) => {
              if (a[each] < b[each]) return -1;
              else if (a[each] == b[each]) return 0;
              else return 1;
            },
          }
        })
      },
      dataSource() {
        return this.versionInfo.metric.data.map((each, index) => {
          let temp = {key: index};
          each.forEach((e, i) => {
            if (i === 3) {
              temp[this.columns[i].dataIndex] = this.cTypeSlot[e];
            } else
              temp[this.columns[i].dataIndex] = e;
          });
          return temp;
        })
      },
      option() {
        let count = {
          updated: 0, unchanged: 0, added: 0, deleted: 0
        };
        this.versionInfo.metric.data.forEach(each => count[each[3]]++);
        let label = ['变化的类', '未变的类', '增加的类', '删除的类'];
        return {
          title: {
            text: '类变更类型占比',
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{b}类数量: {c}'
          },
          legend: {
            type: "scroll",
            orient: 'vertical',
            right: 10,
            data: label
          },
          series: [{
            name: '变化占比',
            type: 'pie',
            data: label.map((each, index) => {
              return {
                name: each,
                value: count[Object.keys(count)[index]]
              }
            })
          }]
        }
      }
    },
    methods: {
      changeTab(key) {
        if (key !== '2') return;
        if (!this.chart) {
          setTimeout(() => {
            let ele = document.getElementById(this.versionInfo.version);
            this.chart = echarts.init(ele);
            this.chart.setOption(this.option);
          }, 0)
        } else
          this.chart.setOption(this.option);
      }
    }
  }
</script>

<style lang="less">
  @import "../assets/glob";

  .chart-wrapper {
    display: flex;
    justify-content: center;
  }

  .ant-table td {
    white-space: nowrap;
  }

  .ant-table th {
    white-space: nowrap;
  }

  .type {
    color: white;
    text-align: center;
    white-space: nowrap;
    border-radius: @radius;
    box-sizing: border-box;
    padding: 2px 10px 2px 10px;
  }

  .type-enum {
    background-color: @primary-orange;
    .type
  }

  .type-class {
    background-color: @primary-blue;
    .type;
  }

  .type-interface {
    background-color: @primary-green;
    .type;
  }
</style>